import React, { Component } from "react";
import axios from "axios";
import "./App.css";
import { Container, Row, Col, Image, Nav } from "react-bootstrap";
import InfiniteScroll from "react-infinite-scroll-component";

class App extends Component {
  state = {
    option: {
      url: "https://api.thecatapi.com/v1/images/search?limit=20&mime_types=gif",
      method: "GET",
      headers: {
        "x-api-key": "11c277f8-7891-4269-aee3-6798cb6b61bd"
      }
    },
    ObjUrls: {}
  };

  componentDidMount() {
    axios(this.state.option).then(res => {
      // console.log(res);
      let urlsObj = {};
      res.data.map(arr => (urlsObj[arr.id] = arr.url));
      //將資料存成物件,方便查詢以及過濾重複資料
      const preKeysObj = this.state.ObjUrls;
      const newKeysObj = { ...preKeysObj, ...urlsObj };
      this.setState({
        ...this.state,
        ObjUrls: newKeysObj
      });
    });
  }

  fetchMoreData = () => {
    axios(this.state.option).then(res => {
      // console.log(res);
      let urlsObj = {};
      res.data.map(arr => (urlsObj[arr.id] = arr.url));
      const preKeysObj = this.state.ObjUrls;
      const newKeysObj = { ...preKeysObj, ...urlsObj };
      this.setState({
        ...this.state,
        ObjUrls: newKeysObj
      });
    });
  };

  render() {
    const colStyle = { textAlign: "center" };
    return (
      <>
        <Nav
          className="justify-content-center"
          activeKey="/home"
          onSelect={selectedKey => alert(`selected ${selectedKey}`)}
          style={{
            boxShadow: "2px 2px 5px RGB(232,232,232)",
            marginBottom: "10px"
          }}
        >
          <Nav.Item>
            <Nav.Link href="/home">Home</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1">Gif</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-2">Img</Nav.Link>
          </Nav.Item>
        </Nav>

        <InfiniteScroll
          dataLength={Object.values(this.state.ObjUrls).length}
          next={this.fetchMoreData}
          hasMore={true}
          loader={<h4>Loading...</h4>}
        >
          <Container>
            <Row>
              {Object.values(this.state.ObjUrls).map((i, index) => (
                <Col xs={12} md={6} lg={4} key={index} style={colStyle}>
                  <Image src={i} fluid roundedCircle thumbnail></Image>
                </Col>
              ))}
            </Row>
          </Container>
        </InfiniteScroll>
      </>
    );
  }
}

export default App;
